.signUpButton, .tick {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
}
  

.signUpButton{
    width: 300px;
    height: 42px;
    background:#9381FF;
    border-radius: 6px;
    transition: all .3s cubic-bezier(0.67, 0.17, 0.40, 0.83);
}
  
.signUpButton svg {
    transform: rotate(180deg);
    transition: all .5s;
}
  
.signUpButton__circle {
    width: 75px;
    height: 75px;
    background: #1DB954;
    border-radius: 50%;
    transform: rotate(-180deg);
}
  
.signUpButton:hover {
    cursor: pointer;
}

.signUpButtonIconContainer {
    color: white;
    font-size: 2em;
    transition: all .9s;
}

#signUpWithEmailContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
}


#emailInput{
    height: 42px;
}


